pace; /* 1 */\n  font-size: 1em; /* 2 */\n}\n\n/* Text-level semantics\n   ========================================================================== */\n\n/**\n * Remove the gray background on active links in IE 10.\n */\n\na {\n  background-color: transparent;\n}\n\n/**\n * 1. Remove the bottom border in Chrome 57-\n * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.\n */\n\nabbr[title] {\n  border-bottom: none; /* 1 */\n  text-decoration: underline; /* 2 */\n  text-decoration: underline dotted; /* 2 */\n}\n\n/**\n * Add the correct font weight in Chrome, Edge, and Safari.\n */\n\nb,\nstrong {\n  font-weight: bolder;\n}\n\n/**\n * 1. Correct the inheritance and scaling of font size in all browsers.\n * 2. Correct the odd `em` font sizing in all browsers.\n */\n\ncode,\nkbd,\nsamp {\n  font-family: monospace, monospace; /* 1 */\n  font-size: 1em; /* 2 */\n}\n\n/**\n * Add the correct font size in all browsers.\n */\n\nsmall {\n  font-size: 80%;\n}\n\n/**\n * Prevent `sub` and `sup` elements from affecting the line height in\n * all browsers.\n */\n\nsub,\nsup {\n  font-size: 75%;\n  line-height: 0;\n  position: relative;\n  vertical-align: baseline;\n}\n\nsub {\n  bottom: -0.25em;\n}\n\nsup {\n  top: -0.5em;\n}\n\n/* Embedded content\n   ========================================================================== */\n\n/**\n * Remove the border on images inside links in IE 10.\n */\n\nimg {\n  border-style: none;\n}\n\n/* Forms\n   ========================================================================== */\n\n/**\n * 1. Change the font styles in all browsers.\n * 2. Remove the margin in Firefox and Safari.\n */\n\nbutton,\ninput,\noptgroup,\nselect,\ntextarea {\n  font-family: inherit; /* 1 */\n  font-size: 100%; /* 1 */\n  line-height: 1.15; /* 1 */\n  margin: 0; /* 2 */\n}\n\n/**\n * Show the overflow in IE.\n * 1. Show the overflow in Edge.\n */\n\nbutton,\ninput { /* 1 */\n  overflow: visible;\n}\n\n/**\n * Remove the inheritance of text transform in Edge, Firefox, and IE.\n * 1. Remove the inheritance of text transform in Firefox.\n */\n\nbutton,\nselect { /* 1 */\n  text-transform: none;\n}\n\n/**\n * Correct the inability to style clickable types in iOS and Safari.\n */\n\nbutton,\n[type=\"button\"],\n[type=\"reset\"],\n[type=\"submit\"] {\n  -webkit-appearance: button;\n}\n\n/**\n * Remove the inner border and padding in Firefox.\n */\n\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n  border-style: none;\n  padding: 0;\n}\n\n/**\n * Restore the focus styles unset by the previous rule.\n */\n\nbutton:-moz-focusring,\n[type=\"button\"]:-moz-focusring,\n[type=\"reset\"]:-moz-focusring,\n[type=\"submit\"]:-moz-focusring {\n  outline: 1px dotted ButtonText;\n}\n\n/**\n * Correct the padding in Firefox.\n */\n\nfieldset {\n  padding: 0.35em 0.75em 0.625em;\n}\n\n/**\n * 1. Correct the text wrapping in Edge and IE.\n * 2. Correct the color inheritance from `fieldset` elements in IE.\n * 3. Remove the padding so developers are not caught out when they zero out\n *    `fieldset` elements in all browsers.\n */\n\nlegend {\n  box-sizing: border-box; /* 1 */\n  color: inherit; /* 2 */\n  display: table; /* 1 */\n  max-width: 100%; /* 1 */\n  padding: 0; /* 3 */\n  white-space: normal; /* 1 */\n}\n\n/**\n * Add the correct vertical alignment in Chrome, Firefox, and Opera.\n */\n\nprogress {\n  vertical-align: baseline;\n}\n\n/**\n * Remove the default vertical scrollbar in IE 10+.\n */\n\ntextarea {\n  overflow: auto;\n}\n\n/**\n * 1. Add the correct box sizing in IE 10.\n * 2. Remove the padding in IE 10.\n */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n  box-sizing: border-box; /* 1 */\n  padding: 0; /* 2 */\n}\n\n/**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n  height: auto;\n}\n\n/**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n\n[type=\"search\"] {\n  -webkit-appearance: textfield; /* 1 */\n  outline-offset: -2px; /* 2 */\n}\n\n/**\n * Remove the inner padding in Chrome and Safari on macOS.\n */\n\n[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n/**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n\n::-webkit-file-upload-button {\n  -webkit-appearance: button; /* 1 */\n  font: inherit; /* 2 */\n}\n\n/* Interactive\n   ========================================================================== */\n\n/*\n * Add the correct display in Edge, IE 10+, and Firefox.\n */\n\ndetails {\n  display: block;\n}\n\n/*\n * Add the correct display in all browsers.\n */\n\nsummary {\n  display: list-item;\n}\n\n/* Misc\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 10+.\n */\n\ntemplate {\n  display: none;\n}\n\n/**\n * Add the correct display in IE 10.\n */\n\n[hidden] {\n  display: none;\n}\n","::-webkit-scrollbar {\n  width: 0.6rem;\n  height: 0.2rem;\n  background-color: $clr-gray-blue-50;\n}\n\n::-webkit-scrollbar-thumb {\n  background-color: $clr-gray-blue-300;\n}\n\n::-webkit-scrollbar-button:vertical:start:decrement,\n::-webkit-scrollbar-button:vertical:end:increment,\n::-webkit-scrollbar-button:horizontal:start:decrement,\n::-webkit-scrollbar-button:horizontal:end:increment {\n  display: none;\n}\n\n* {\n  scrollbar-color: $clr-gray-blue-300 $clr-gray-blue-50;\n  scrollbar-width: thin;\n}","/* typography */\n$family: 'Sora', sans-serif;\n$fs-sx: 0.6rem;\n$fs-sm: 0.8rem;\n$fs-normal: 1rem;\n$fs-md: 1.1rem;\n$fs-lg: 1.5rem;\n$fs-xl: 2rem;\n$fs-xxl: 2.5rem;\n\n$fw-thin: 200;\n$fw-normal: 400;\n$fw-bold: 600;\n\n/* colors */\n$clr-white: hsl(0, 0%, 98%);\n$clr-accent: hsl(220, 98%, 61%);\n$clr-warning: hsl(0, 70%, 55%);\n\n$clr-gray-blue-50: hsl(234, 39%, 85%);\n$clr-gray-blue-100: hsl(236, 33%, 92%);\n$clr-gray-blue-200: hsl(233, 11%, 84%);\n$clr-gray-blue-300: hsl(236, 9%, 61%);\n$clr-gray-blue-400: hsl(235, 19%, 35%);\n\n$clr-blue-300: hsl(234, 11%, 52%);\n$clr-blue-400: hsl(233, 14%, 35%);\n$clr-blue-500: hsl(237, 14%, 26%);\n$clr-blue-600: hsl(235, 24%, 19%);\n$clr-blue-800: hsl(235, 21%, 11%);\n\n$clr-gradient: linear-gradient(135deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%));\n\n/* other */\n$content-max-width: 992px;\n$vertical-padding: 1rem;\n\n$radius: 0.5rem;\n$radius-input: 0.25rem;\n\n$shadow-sm: 0 4px 8px hsla(0, 0%, 0%, 0.25), 0 3px 3px hsla(0, 0%, 0%, 0.22);\n$shadow-md: 0 7px 12px hsla(0, 0%, 0%, 0.25), 0 6px 6px hsla(0, 0%, 0%, 0.22);\n$shadow-lg: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px hsla(0, 0%, 0%, 0.22);\n$shadow-negative-sm: 0 -4px 8px hsla(0, 0%, 0%, 0.25), 0 -3px 3px hsla(0, 0%, 0%, 0.22);\n$shadow-negative-md: 0 -7px 12px hsla(0, 0%, 0%, 0.25), 0 -6px 6px hsla(0, 0%, 0%, 0.22);\n\n$transition: 0.2s;\n","@import './styles/normalize';\n@import url('https://fonts.googleapis.com/css2?family=Sora:wght@200;400;600&display=swap');\n@import './styles/commonVariables';\n@import './styles/scrollBar';\n\n\n*,\n*::before,\n::after {\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n}\n\nbody[data-theme-type='light'] {\n  --clr-text: #{$clr-gray-blue-400};\n  --clr-bg: #{$clr-gray-blue-100};\n  --clr-bg-ui: #{$clr-white};\n  --clr-ui: #{$clr-gray-blue-200};\n  --clr-ui-hover: #{$clr-blue-300};\n}\n\nbody[data-theme-type='dark'] {\n  --clr-text: #{$clr-gray-blue-200};\n  --clr-bg: #{$clr-blue-800};\n  --clr-bg-ui: #{$clr-blue-500};\n  --clr-ui: #{$clr-gray-blue-400};\n  --clr-ui-hover: #{$clr-gray-blue-300};\n}\n\nbody {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  min-height: 100vh;\n  font-family: $family;\n  font-size: $fs-md;\n  color: var(--clr-text);\n  background-color: var(--clr-bg);\n}\n\n.header {\n  position: sticky;\n  top: 0;\n  width: 100%;\n  background-color: var(--clr-bg-ui);\n  padding-inline: $vertical-padding;\n  box-shadow: $shadow-sm;\n  z-index: 2;\n\n  &__content {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    gap: 2em;\n    max-width: $content-max-width;\n    margin-inline: auto;\n  }\n\n  &__title {\n    font-size: clamp($fs-lg, 5vw, $fs-xl);\n    line-height: 1.1;\n    padding-block: 0.5em;\n    margin: 0;\n  }\n}\n\n.main {\n  display: flex;\n  justify-content: center;\n  flex: 1;\n  width: 100%;\n  padding: 0.5em;\n\n}\n\n.calculator {\n  width: 100%;\n  max-width: $content-max-width;\n  padding: 0.5em;\n\n  &__controls {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    gap: 1em;\n\n    @media (max-width: 670px) {\n      flex-direction: column;\n      justify-content: center;\n      gap: 0.4em;\n    }\n  }\n\n  &__base-currency {\n    text-align: right;\n    padding-top: 0.7em;\n\n    @media (max-width: 440px) {\n      text-align: left;\n    }\n  }\n\n  &__base-currency-abbreviation {\n    font-weight: $fw-bold;\n  }\n\n  &__currencies-list {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\n    justify-content: center;\n    gap: 0.8em;\n    padding-block: 0.5em;\n    list-style: none;\n  }\n  &__currencies-list-warning {\n    font-size: $fs-lg;\n    font-weight: $fw-bold;\n    text-align: center;\n    padding-block: 1em;\n  }\n\n  &__additional-info {\n    display: flex;\n    flex-direction: column;\n    gap: 0.3em;\n    padding-top: 0.7em;\n  }\n\n  &__additional-text {\n    font-weight: $fw-thin;\n  }\n}\n\n.footer {\n  width: 100%;\n  background-color: var(--clr-bg-ui);\n  box-shadow: $shadow-negative-sm;\n  padding-block: 0.6em;\n  text-align: center;\n\n  &__copyright {\n    line-height: 1.1;\n  }\n}\n\n@import './styles/spinner';\n@import './styles/snackBar';\n@import './styles/themeSwitcher';\n@